*{
    box-sizing: border-box;
}

body{
    margin: none;
}

.nav{
    background-color: #333;
    color: white;
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 55rem;
    padding: 0 1rem;
}

.nav a{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0.25 rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.siteTitle{
    font-size: 4rem;
}

.link{
    font-size: 2rem;
}

.nav li.active{
    background-color: #555;
}

.nav li:hover{
    background-color: #777;
}



